import React from 'react';
import Link from 'gatsby-link';


const Footer = () =>
  <footer>
    <div style={{ margin: '0 auto', width: '90%' }}>
      <p>&copy; 2018 <Link to="https://www.ingredienza.ch">Ingredienza</Link></p>
    </div>
  </footer>

export default Footer



